	import uniLoadMore from "../../components/uni-load-more/uni-load-more.vue"
	export default{
	    components: {uniLoadMore},
		data() {
			return {
	            ismore:false,
	            contentText: {
	                contentdown: '查看更多',
	                contentrefresh: '加载中',
	                contentnomore: '没有更多',
	            },
	            page:1,
	            size:10,
				searchValue: '',
				searchList: [],
				classifyId:'',
				classifyName:'',
				type:''
			};
		},
		onLoad(options) {
			this.searchValue = options.value
			this.classifyName = options.classifyName
			this.classifyId= options.classifyId
			this.type = options.type;
			this.searchProduct(this.searchValue)
		},
        onReachBottom() {
            console.log('onReachBottom')
            this.status = 'loading'
            uni.showNavigationBarLoading()
            
            this.page++
            this.searchProduct(this.searchValue)
        },
		methods: {
			searchProduct(input) {
				this.$api.communityProductList({
					size: this.size,
					page: this.page,
					keywords: input,
					classifyId:this.classifyId?this.classifyId:'',
				}).then(res => {
					console.log(res);
                    
					if (res.status == "OK") {
                        if(this.page == 1){
                            this.searchList = []
                            if(res.list.length>=this.size){
                                this.ismore = true
                            }
                        }
						res.list.map(item => {
							this.searchList.push({
								price: item.price / 100,
								originalPrice: item.originalPrice / 100,
								name: item.name,
								pic: JSON.parse(item.icon)[0].url,
								id: item.id
							})
						})
					}
                    uni.stopPullDownRefresh();
                    uni.hideNavigationBarLoading()
				}).catch(err => {
					console.log(err);
				})
			},
			search(res) {
                this.searchValue = res.value
                this.page = 1
				this.searchProduct(this.searchValue)
			},
			goDetail(id){
				uni.navigateTo({
					url: `/pages/health-product-detail/health-product-detail?id=${id}&type=${this.type}`,
				});
			},
		},
		filters: {
			toFixed2: function(value) {
				return value.toFixed(2);
			},
		}
	}
